<template>
    <div class="f5">
        <van-nav-bar title="我的卫生检查记录" />
            <van-list
                v-model="loading"
                :finished="finished"
                :immediate-check="false"
                finished-text="没有更多了"
                @load="onLoad"
                :offset="10"
            >
            <van-cell-group v-for="(item,index) in checklist" :key="index">
                <van-cell title="房间号" :value="item.room_num" />
                <van-cell title="检查类型" :value="item.check_type" />
                <van-cell title="检查结果" :value="item.check_state" />
                <van-cell v-if="item.punish_money!=0" title="奖罚金额" :value="item.punish_money" />
                <van-cell title="检查时间" :value="item.check_date" />
                <van-cell  title="检查图片">
                    <van-button  size="small" type="info" style="margin:0 auto" @click="getimg(item.img)">查看图片</van-button>
                </van-cell>
               
            </van-cell-group>
        </van-list>
    </div>
</template>

<script>
import { Toast } from 'vant';
import { ImagePreview } from 'vant';
import {apicheckhistorylist} from "@/api/index"
export default {
    name:'myRecord',
    data() {
        return {
            loading: false,
            finished: false,
            page: 1, //请求第几页
            pageSize: 5, //每页请求的数量
            total: 0, //总共的数据条数
            checklist:[]
        }
    },
    methods: {
        // 上拉加载
        onLoad() {
            this.page++;
            this.getlist();
        },
        getlist(){
            let params = {
                page: this.page,
                pageSize: this.pageSize
            };
            apicheckhistorylist(params).then(res=>{
                this.loading = false;
                this.total = res.data.count; 
                let rows = res.data.data;
                if (rows == null || rows.length === 0) {
                // 加载结束
                this.finished = true;
                    return;
                }
                this.checklist = this.checklist.concat(rows);
                this.isLoading = false;
                if (this.checklist.length >= this.total) {
                    this.finished = true;
                }
            })
        },
        getimg(imgarr){
            if(imgarr.length==0){
                 Toast("暂无图片")
            }else{
                ImagePreview(imgarr);
            }  
        }
    },
    created(){
        this.getlist();
    }
}
</script>

<style>

</style>


